.App {
  text-align: center;
  position: relative;
  z-index: 10;
}
.logo{
  font-family: 'Big Shoulders Inline Display', cursive !important; 
  letter-spacing: 5px;
  color: white;
  text-decoration: none;
}
.logo:hover{
  text-decoration: none;
  color: aqua;
}
.line{
  box-shadow: 0 1px 12px 0 rgb(139, 139, 139)
}
.main-container{
  min-height: calc(100vh - 72px);
}
.App-header {
  background-color: transparent;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}
.App-link {
  color: #61dafb;
}
.title{
  font-size: calc(5rem + 2vmin);
  color: black;
  -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: rgb(127, 160, 190);
  animation: anima 1s ease-in-out;
  text-shadow: 0 1px 50px rgb(107, 110, 136);
  transition: .3s ease-in-out;
}
@keyframes anima {
  0%{
    opacity: 0;
    transform: translateY(20px) scale3d(4, -3, 0.7);
    text-shadow: 0 1px 10px rgb(107, 110, 136);
  }
  100%{
    opacity: 1;
    transform: translateY(0) scale3d(1, 1, 1);
    text-shadow: 0 1px 50px rgb(107, 110, 136);
  }
}

.subtitle{
  font-weight: 100;
  letter-spacing: 2px;
  animation: anima 1s ease-in-out;
  animation-duration: 1.3s;
}
.btn-container{
  width: 25%;
  margin: 2rem 0;
  animation: btn-anima 20s ease-in-out infinite;
  animation-duration: 5s;
  transition: all .3s ease-in-out;
}
.btn-container .btn , .title , .subtitle{
  transform: scale(1);
  transition: all .3s ease-in-out;
}
.btn-container .btn{
  transform: scale(1);
  transition:  .3s ease-in-out;
  animation: btn-fade all 1s ease-in-out;
  animation-duration: 2s;
  -webkit-transition: btn-fade all .5s ease-in-out;
  -moz-transition: btn-fade all .5s ease-in-out;
  transition: btn-fade all .5s ease-in-out;
}
.btn-container .btn:hover , .title:hover , .subtitle:hover{
  transform: scale(1.1);
  transition: all .3s ease-in-out;
}
.btn-container .btn a {
  color: white;
  text-decoration: none;
  color: lightskyblue;
  padding: 3rem ;
}
.btn-container .btn a:hover {
  color: white;
  text-decoration: none;
  color: rgb(255, 255, 255);
  animation: none;
  font-weight: 500;
  transform: scale(1.2);
}
@keyframes btn-anima {
  0%{
    transform: translateY(50%) scale(0.9);
    background-color: rgba(86, 86, 168, 0.15);
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    transition: all .2s;
  }
  30%{
    transform: translateY(0);
    opacity: 1;
    background-color: transparent;
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    transition: all .2s;
  }
  70%{
    transform: translateY(0);
    opacity: 1;
    background-color: transparent;
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    transition: all .2s;
  }
  100%{
    transform: translateY(50%) scale(0.9);
    opacity: 0.8;
    background-color: rgba(86, 86, 168, 0.15);
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
}
@keyframes btn-fade {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@keyframes mobile-anima {
  0%{
    opacity: 0;
    transform: translateY(30px) ; 
    text-shadow: 0 1px 10px rgb(107, 110, 136);
  }
  /* //scale3d(4, -3, 0.7) */
  50%{
    opacity: 1;
    transform: translateY(-20px) scale3d(1.1, 1.1, .5);
    text-shadow: 0 1px 50px rgb(107, 110, 136);
  }
  100%{
    opacity: 1;
    transform: translateY(0) ;
    text-shadow: 0 1px 50px rgb(107, 110, 136);
  }
  /* scale3d(1, 1, 1) */
}
@media only screen and (max-width: 800px) {
  body {
    background-color: lightblue;
  }
  .btn-container{
    width: 50%;
  }
  .btn-container .btn a {
    padding: 0;
  }
  .title{
    animation: mobile-anima .5s ease-in-out;
  }
  .subtitle{
    animation: mobile-anima 1s ease-in-out;
    animation-duration: 1s;  
  }
}

